import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

import "../css/AppNavbar.css";

import { Link } from "react-router-dom";

function AppNavbar() {
  return (
    <Container className="fixed-bottom appNavbar" fluid>
      <Row style={{ height: "70px" }}>
        <Col style={{ padding: 0 }}>
          <Button
            style={{
              margin: 0,
              width: "100%",
              height: "100%",
              borderRadius: 0,
              border: "none",
              backgroundColor: "white",
            }}
          >
            <Link className="nav-link" to="/">
              <i
                className="fa-solid fa-lg fa-home"
                style={{ color: "#D68C45" }}
              ></i>
            </Link>
          </Button>
        </Col>
        <Col style={{ padding: 0 }}>
          <Button
            style={{
              margin: 0,
              width: "100%",
              height: "100%",
              borderRadius: 0,
              border: "none",
              backgroundColor: "white",
            }}
          >
            <Link className="nav-link" to="/scanexpiration">
              <i
                className="fa-solid fa-lg fa-magnifying-glass"
                style={{ color: "#D68C45" }}
              ></i>
            </Link>
          </Button>
        </Col>
        <Col style={{ padding: 0 }}>
          <Button
            style={{
              margin: 0,
              width: "100%",
              height: "100%",
              borderRadius: 0,
              border: "none",
              backgroundColor: "white",
            }}
          >
            <Link className="nav-link" to="/">
              <i
                className="fa-solid fa-lg fa-list"
                style={{ color: "#D68C45" }}
              ></i>
            </Link>
          </Button>
        </Col>
        <Col style={{ padding: 0 }}>
          <Button
            style={{
              margin: 0,
              width: "100%",
              height: "100%",
              borderRadius: 0,
              border: "none",
              backgroundColor: "white",
            }}
          >
            <Link className="nav-link" to="/">
              <i
                className="fa-solid fa-lg fa-user"
                style={{ color: "#D68C45" }}
              ></i>
            </Link>
          </Button>
        </Col>
      </Row>
    </Container>
  );
}

export default AppNavbar;
